import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { saveBooking, toggleChecked, getRooms } from "../ducks/store";
import React, { Component } from "react";
import styled from "styled-components";
import BookingForm from "./BookingForm";

class Booking extends Component {
  render() {
    // console.log(this.props);
    const { saveBooking, toggleChecked } = this.props;
    return (
      <Wrapper>
        <BookingForm rooms={this.props.rooms} toggleChecked={toggleChecked} />
        <Button onClick={saveBooking}> Submit</Button>
      </Wrapper>
    );
  }
}

const mapDispatchToProps = dispatch =>
  bindActionCreators({ saveBooking, toggleChecked, getRooms }, dispatch);

const mapStateToProps = state => {
  return {
    rooms: state.rooms
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Booking);

const Wrapper = styled.section``;
const Button = styled.button`
  background: lightGrey;
  margin-top: 20px;
  padding: 5px;
  font-size: 16px;
`;
